@import "mixins/formslider";

@include with-formslider {
  input.without-spinner::-webkit-outer-spin-button,
  input.without-spinner::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0; }

  @include with-formslider-slide {
    text-align: center;
    @include with-answers {
      @include with-answer {
        display: inline-block;
        vertical-align: top;
        float: left;
        cursor: pointer;
        > * {
          cursor: pointer; }

        @include with-answer-inner {
          display: block; } }

      @include with-answer-input {
        display: none; } }

    @include with-answer-count(1) {
      @include with-answer {
        width: 100%; } }

    @include with-answer-count(2) {
      @include with-answer {
        width: 50%; } }

    @include with-answer-count(3) {
      @include with-answer {
        width: 33.33%; } }

    @include with-answer-count(4) {
      @include with-answer {
        width: 25%; } } } }
